#contact {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 5%;
  padding-bottom: 5%;
  padding-right: 5%;
  padding-top: 5%;
}

.contact-title {
  font-weight: 500;
  font-family: "Figtree", sans-serif;
  color: #8892b0;
  font-size: 40px;
}

.contact-description {
  font-size: 20px;
  font-weight: 100;
  font-family: "Figtree", sans-serif;
  padding-top: 30px;
}

.contact-links {
  margin-top: 5%;
}

.contact-icon {
  width: 7%;
  height: auto;
  padding-right: 5%;
}
