#navbar {
  display: flex;
  flex-direction: row;
  justify-content: right;
}

a {
  text-decoration: none;
  color: #e6f1ff;
  font-size: 22px;
  font-weight: 400;
  flex-direction: row;
  align-items: flex-end;
  padding-top: 10px;
}

ul {
  list-style-type: none;
  display: flex;
}

li {
  margin-right: 40px;
}

button {
  border-radius: 30px;
  font-family: "Figtree", sans-serif;
  font-size: 20px;
  height: 50px;
  width: 150px;
  border: none;
  margin-bottom: 10px;
  padding: 0px;
  font-weight: 500;
  background-color: #7ef5bb;
  will-change: transform;
  transition: transform 450ms;
}

button:hover {
  transition: transform 125ms;
  transform: translateY(-5px);
  box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
